import React, {useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import {tileData} from '../staticData/tileData';
import Header from "../components/Header/Header";

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: theme.palette.background.paper,
    },
    gridContainer: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        padding: '88px 20px 20px',
    },
    gridList: {
        width: 500,
        height: 450,
        // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
        transform: 'translateZ(0)',
    },
    // titleBar: {
    //     background:
    //         'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' +
    //         'rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
    // },
    icon: {
        color: 'white',
    },
    tile: {
        border: '1px solid #000'
    }
}));

export default function GalleryPage() {
    const classes = useStyles();
    const [open, setOpen] = useState(false);

    const handleOpenNavBar = () => {
        setOpen(!open);
    }
    return (
        <div className={classes.root}>
            <div id="st-container" className={open?"st-container st-effect-1 st-menu-open":"wrapper dashboard-wrapper st-container"}>
                <Header
                    header="Gallery Sandbox"
                    openNavBar={handleOpenNavBar}
                    open={open} />
                <div className={classes.gridContainer}>
                    <GridList cellHeight={200} spacing={1} className={classes.gridList}>
                        {tileData.map((tile, index) => (
                            <GridListTile
                                key={index}
                                cols={tile.featured ? 2 : 1}
                                rows={tile.featured ? 2 : 1}
                                className={classes.tile}>
                                <img src={tile.img} alt={tile.title} />
                                <GridListTileBar
                                    title={tile.title}
                                    titlePosition="top"
                                    actionIcon={
                                        <IconButton aria-label={`star ${tile.title}`} className={classes.icon}>
                                            <StarBorderIcon />
                                        </IconButton>
                                    }
                                    actionPosition="left"
                                    className={classes.titleBar}
                                />
                            </GridListTile>
                        ))}
                    </GridList>
                </div>
            </div>
        </div>
    );
}
